@mixin flexer($direction) {
    display: flex;
    flex-direction: $direction;
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
* {
    font-family: "Montserrat", sans-serif;
    margin: 0;
}


.reg-table {
    .reg-item {
        background-color: rgb(205, 227, 236);
        width: 100%;
        padding: 10px 1rem;
        border-radius: 5px;
        margin-bottom: 5px;
        &:last-of-type {
            margin-bottom: 0;
        }
        .sl-num {
            font-size: 2rem;
            margin-right: 1rem;
        }
        .label {
            color: rgb(122, 147, 122);
        }
        .reg-info {
            margin-right: 2rem;
            .reg-id {
                font-size: 1.2rem;
            }
            .applied-at {
                color: rgb(126, 139, 155);
                font-size: 0.8rem;
            }
            .ip-addr {
                font-size: 0.8rem;
                color: rgb(122, 147, 122);
            }
        }
        .trx-id {
            .value {
                padding: 5px 10px;
                background-color: rgb(255, 255, 255);
                border-radius: 5px;
                box-shadow: 0 2px 5px rgba(163, 181, 184, 0.56);
            }
        }
        .team-info {
            flex-grow: 1;
        }
    }
    .reg-item.approved {
        background-color: rgb(235, 235, 235)
    }
    
}


.bg-red {
    background-color: red;
}


.flex-row {
    @include flexer(row);
}

.flex-col {
    @include flexer(column);
}

.justify-content-center {
    justify-content: center;
}

.align-items-center {
    align-items: center;
}

.me-1 {
    margin-right: 1rem;
}
.me-2 {
    margin-right: 2rem;
}